@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  height: 100%;
  overflow: hidden;
}

.App__footer {
  margin-top: 5%;
  width: 100vw;
  height: 90%;
  text-align: center;
}

.App__footer_img {
  width: 80%;
  margin: auto;
}

.App__footer_body {
  margin-top: 1%;
}

.App__footer_text {
  color: #373A56;
}

.App__banner {
  width: 7%;
  left: 4%;
  position: absolute;
  z-index: 10000;
}


@media screen and (max-width: 414px){
  .App__footer_img {
    margin-top: 30%;
    width: 230%;
    -webkit-transform: translateX(-260px);
            transform: translateX(-260px);
  }

  .App__banner {
    display: none;
  }
}

.Home {
  width: 100vw;
  height: 100vh;
  padding: 0%;
  background-color: #ececee;
  overflow: hidden;
}

.Home__header-main {
  font-size: 60px;
  font-weight: 750;
  color: #412B73;
  padding-top: 2%;
}

.Home__header-sub {
  color: #373A56;
  font-size: 24px;
  margin-top: 2%;
}

.Home__body {
  color: #A6A6B4;
  font-size: 15px;
  margin: 0%;
  padding: 0;
  margin-top: 0.5%;
}

.Home__button-subscribe {
  background-color: #FBBC89;
  color: white;
  border: none;
  border-radius: 40px;
  font-size: 15px;
  padding-left: 6%;
  padding-right: 6%;
  padding-top: 2%;
  padding-bottom: 2%;
  margin-top: 4%;
  font-weight: 750;
  letter-spacing: 2px;
  -webkit-transition: background-color 0.5s;
  transition: background-color 0.5s;
  cursor: pointer;
}

.Home__button-subscribe:hover {
  color: rgba(255, 255, 255, 0.7);
  background-color: rgba(254, 145, 77, 0.7);
}

.Home__image {
  padding-top: 5%;
  width: 90%;
}

.Home__right {
  padding-top: 10%;
}

.Home__icon {
  z-index: 10000;
  font-size: 22px;
  color: #373A56;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
}

.Home__icon:hover {
  color: rgba(55, 58, 86, 0.4);
}

.Home__social-container {
  margin-top: 4%;
}

@media only screen and (max-width: 600px) {
  .Home__header-mini {
    font-size: 20px;
  }

  .Home__header-main {
    font-size: 80px;
  }

  .Home__header-subMain {
    font-size: 20px;
  }

  .Home__button-subscribe {
    margin-top: 4%;
  }

  .Home__mobile-container {
    width: 100%;
    text-align: left;
  }

  .Home__image {
    margin: auto;
    width: 50%;
    margin-left: 25%;
  }

  .faqTitle{
    font-size: 40px;
    font-weight: 750;
    color: #373A56;
  }

  /* /* .primaryFaq{
    font-size: 30px;
  } */
  .subfaqtitle{
    font-size: 30px;
  }
  .title{
    font-size: 30px;
  }
}

@media screen and (max-width: 414px){


  .App__right{
    padding-left: 10%;
  }
  .App__header-main {
    font-size: 30px;
  }
  .App__image {

    width: 80%;
    padding-right: 40%;
  }

  .Home__header-main {
    text-align: center;
  }
  .Home__header-sub {
    text-align: center;
  }
  .Home__body {
    text-align: center;
  }
  .Home__social-container {
    text-align: center;
  }
  .Home__mobile-container {
    text-align: center !important;
  }


}

.navbar__container {
  background-color: #ececee;
  float: right;
  margin-right: 5%;
  margin-top: 1.4%;
}

a:hover { color:#000 !important;}

.buttons {
  color: #696969;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.10em;
  background-color: Transparent;
  border: none;
  padding: 15px;
  position: relative;
}

.buttons:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #412B73;
  -webkit-transition: width 0.3s ease 0s, left 0.3s ease 0s;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.buttons:hover:after {
  width: 100%;
  left: 0;
}

button:focus {outline:0;}

.mobileAbout{
  display: none;
}

.About__section {
  width: 100vw;
}

.About__header {
  font-weight: 750;
  color: #412B73;
  padding-top: 2%;
  font-size: 45px;
}

.About__description{
  color: #373A56;
  margin-top: 4%;
  font-size: 18px;
  font-weight: 350;
  line-height: 1.8;
  width: 85%;
}

.About__imageContainer {
  width: 100%;
  padding-right: 10%;
}

.About__image {
  float: right;
  width: 82%;
}

.About__boxes {
  margin-top: 8%;
  width: 100%;
}

.About__box {
  height: 100%;
  box-shadow: 0px 2px 15px rgba(129, 129, 129, 0.15);
  margin-right: 4%;
  margin-left: 2%;
  text-align: center;
  padding: 4%;
  padding-bottom: 3%;
  border-radius: 30px;
  background-color: white;
}

.About__box_title1, .About__box_title2, .About__box_title3 {
  font-size: 42px;
}

.About__box_title1 {
  color: #412B73;
  font-weight: 750;
  margin: 0;
}

.About__box_title2 {
  color: #ABC13B;
  font-weight: 750;
  margin: 0;
}

.About__box_title3 {
  color: #FBBC89;
  font-weight: 750;
  margin: 0;
}

.About__box_text {
  font-weight: 300;
  font-size: 16px;
}

.About__dots {
  position: absolute;
  left: 6%;
}

.About__dots2 {
  position: absolute;
  top: 0;
  right: -10%;
}
@media screen and (max-width: 414px){
  .about{
    display: none;
  }
  .mobileAbout{
    display: inline;
    display: initial;
  }
  .col-7{
    max-width: 100% !important;
    width: 100% !important;
    display: none;
  }
  .col-5{display: none;}
  .About__header {
    text-align: center;
  }
  .About__description{
    text-align: center;
    padding-left: 14%;
    margin-bottom: 10%;
  }
  .About__image {
    width: 100%;
  }
  .About__box {
    width: 40%;
    margin-left: 30%;
    margin-bottom: 10%;
  }


}
.Faq {
  width: 100vw;
  height: 85vh;
  margin-top: 10%;
}

.flex {
  display: flex;
}

.Faq__header {
  font-weight: 750;
  color: #412B73;
  font-size: 45px;
  text-align: center;
  width: 100%;
}

.Faq__title {
  width: 100%;
  text-align: center;
}

.Faq__col {
  text-align: center;
}
.faqMobile{
  display: none;
}

