.sponsorsMobile {
  display: none;
}

.Sponsor__parent {
  width: 100vw;
}

.Sponsor__header {
  font-weight: 750;
  color: #412B73;
  font-size: 45px;
  text-align: center;
  width: 100%;
}

.Sponsor__container {
  text-align: center;
  margin-top: 2%;
}

.Sponsor__planet {
  position: absolute;
  left: 6%;
}

.asucr {
  width: 25%;
}

.beans {
  margin-left: 2%;
  margin-right: 2%;
  width: 14%;
}

.blsp {
  width: 40%;
  margin-right: 2%;
}

.wa {
  width: 30%;
}

.xyz {
  width: 12%;
}

.library {
  width: 20%;
}

.ieee {
  width: 15%;
}

.acm {
  width: 14%;
  margin-left: 4%;
}

@media screen and (max-width: 414px){
  .notMobile{
    display: none;
  }
  .sponsorsMobile{
    display: initial;
  }
  .Sponsor__parent {
    height: 30vh;
  }
  .asucr {
    width: 35%;
  }

  .beans {
    margin-left: 0%;
    margin-right: 0%;
    width: 25%;
  }

  .blsp {
    width: 62%;
    margin-right: 0%;
  }

  .wa {
    width: 40%;
  }

  .xyz{
    width: 32%;
  }
}
